 export function isMobile(){
    if (/(iPhone|iPad|iPod|iOS|Android)/i.test(navigator.userAgent)) { 
           return true;
     }
     return false;
  }

export function watermark(settings,container) {
          
    //
     var defaultSettings={
         watermark_txt:"text",
         watermark_x:1,//x
         watermark_y:1,//Y
         watermark_rows:10,//
         watermark_cols:20,//
         watermark_x_space:1,//x
         watermark_y_space:50,//y
         watermark_color:'#AAAAAA',//
         watermark_alpha:0.2,//
         watermark_fontsize:'25px',//
         watermark_font:'STKaiti',//
         watermark_width:80,//
         watermark_height:40,//
         watermark_angle:20//
     };
     //jquery.extend
     Object.assign(defaultSettings, settings);
     var oTemp = document.createDocumentFragment();
  
     //
     var page_width = Math.max(document.body.scrollWidth,document.body.clientWidth);
     //
     var page_height = Math.max(document.body.scrollHeight,document.body.clientHeight);
  
     //0x
     if (defaultSettings.watermark_cols == 0 || (parseInt(defaultSettings.watermark_x + defaultSettings.watermark_width *defaultSettings.watermark_cols + defaultSettings.watermark_x_space * (defaultSettings.watermark_cols - 1)) > page_width)) {
         defaultSettings.watermark_cols = parseInt((page_width-defaultSettings.watermark_x+defaultSettings.watermark_x_space) / (defaultSettings.watermark_width + defaultSettings.watermark_x_space));
         defaultSettings.watermark_x_space = parseInt((page_width - defaultSettings.watermark_x - defaultSettings.watermark_width * defaultSettings.watermark_cols) / (defaultSettings.watermark_cols - 1));
       }
     //0y
     if (defaultSettings.watermark_rows == 0 || (parseInt(defaultSettings.watermark_y + defaultSettings.watermark_height * defaultSettings.watermark_rows + defaultSettings.watermark_y_space * (defaultSettings.watermark_rows - 1)) > page_height)) {
         defaultSettings.watermark_rows = parseInt((defaultSettings.watermark_y_space + page_height - defaultSettings.watermark_y) / (defaultSettings.watermark_height + defaultSettings.watermark_y_space));
         defaultSettings.watermark_y_space = parseInt(((page_height - defaultSettings.watermark_y) - defaultSettings.watermark_height * defaultSettings.watermark_rows) / (defaultSettings.watermark_rows - 1));
     }
     var x;
     var y;
     for (var i = 0; i < defaultSettings.watermark_rows; i++) {
         y = defaultSettings.watermark_y + (defaultSettings.watermark_y_space + defaultSettings.watermark_height) * i;
         for (var j = 0; j < defaultSettings.watermark_cols; j++) {
             x = defaultSettings.watermark_x + (defaultSettings.watermark_width + defaultSettings.watermark_x_space) * j;
  
             var mask_div = document.createElement('div');
             mask_div.id = 'mask_div' + i + j;
             mask_div.appendChild(document.createTextNode(defaultSettings.watermark_txt));
             //div
             mask_div.style.webkitTransform = "rotate(-" + defaultSettings.watermark_angle + "deg)";
             mask_div.style.MozTransform = "rotate(-" + defaultSettings.watermark_angle + "deg)";
             mask_div.style.msTransform = "rotate(-" + defaultSettings.watermark_angle + "deg)";
             mask_div.style.OTransform = "rotate(-" + defaultSettings.watermark_angle + "deg)";
             mask_div.style.transform = "rotate(-" + defaultSettings.watermark_angle + "deg)";
             mask_div.style.visibility = "";
             mask_div.style.position = "absolute";
             mask_div.style.left = x + 'px';
             mask_div.style.top = y + 'px';
             mask_div.style.overflow = "hidden";
             mask_div.style.zIndex = "999";
             //mask_div.style.border="solid #eee 1px";
             mask_div.style.pointerEvents='none';
             mask_div.style.opacity = defaultSettings.watermark_alpha;
             mask_div.style.fontSize = defaultSettings.watermark_fontsize;
             mask_div.style.fontFamily = defaultSettings.watermark_font;
             mask_div.style.color = defaultSettings.watermark_color;
             mask_div.style.textAlign = "center";
             mask_div.style.width = (defaultSettings.watermark_width-20) + 'px';
             mask_div.style.height = (defaultSettings.watermark_height-20) + 'px';
             mask_div.style.display = "block";
             oTemp.appendChild(mask_div);
         };
     };
     document.body.appendChild(oTemp);
  }
  
  
  export function addWaterMarker({
    //  ES6 
    //  https://developer.mozilla.org/zh-CN/docs/Web/JavaScript/Reference/Functions/Default_parameters
    container = document.body,
    width = '220px',
    height = '220px',
    textAlign = 'center',
    textBaseline = 'middle',
    font = "16px  'Microsoft YaHei'",
    fillStyle = 'rgba(170, 170, 170, 0.2)',
    content = '',
    rotate = '-30',
    zIndex = 1000
  } = {}) {
    var args = arguments[0];
    var canvas = document.createElement('canvas');
  
    canvas.setAttribute('width', width);
    canvas.setAttribute('height', height);
    canvas.setAttribute('style', `
      position:fixed;
      top:0;
      background:#fff;
      z-index:-1;`);
    var ctx = canvas.getContext("2d");
  
    ctx.textAlign = textAlign;
    ctx.textBaseline = textBaseline;
    ctx.font = font;
    ctx.fillStyle = fillStyle;
    ctx.rotate(Math.PI / 180 * rotate);
    ctx.fillText(content, parseFloat(width) / 2, parseFloat(height) / 2);
  
    var base64Url = canvas.toDataURL();
    const watermarkDiv = document.createElement("div");
    watermarkDiv.setAttribute('style', `
      position:absolute;
      top:0;
      left:0;
      width:100%;
      height:100%;
      z-index:${zIndex};
      pointer-events:none;
      background-repeat:repeat;
      background-image:url('${base64Url}')`);
  
    container.style.position = 'relative';
    container.insertBefore(watermarkDiv, container.firstChild);
  
    
  }
  
/**
 * 
 * @param {*} hasWeek 
 * @param {*} type    1  20210428103123 2  2021428 10:31:23
 */
export function getDateTime(  type = 1,hasWeek=false){
  //
  var date_time = new Date();
  //
  var week;
//switch
switch (date_time.getDay()){
case 1: week=""; break;
case 2: week=""; break;
case 3: week=""; break;
case 4: week=""; break;
case 5: week=""; break;
case 6: week=""; break;
default:week=""; break;
}
//
var year = date_time.getFullYear();
//100
if(year<10){
year="0"+year;
}
//
var month = date_time.getMonth()+1;
//100
if(month<10){
month="0"+month;
}
//
var day = date_time.getDate();
//100
if(day<10){
day="0"+day;
}
//
var hours =date_time.getHours();
//100
if(hours<10){
hours="0"+hours;
}
//
var minutes =date_time.getMinutes();
//100
if(minutes<10){
minutes="0"+minutes;
}
//
var seconds=date_time.getSeconds();
//100
if(seconds<10){
seconds="0"+seconds;
}
              switch(type){
                case 1:
                  return  year+month+day+hours+minutes+seconds;
                  break;
                case 2:
                  return  (year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds)+" "+ (hasWeek ? week : '');
                  break;
              }
}


/**
 * 
 * @param {*}  
 * @param {*}  
 * @param {*} 
 * @returns 
 */
 export function fuzzyQuery(list,colmn, keyWord) {
  var reg =  new RegExp(keyWord);
  var arr = [];
  for (var i = 0; i < list.length; i++) {
    if (reg.test(list[i][colmn])) {
      arr.push(list[i]);
    }
  }
  return arr;
}